@mixin head-cate {
    .head-cate {
        background: #F6F6F6;
        height: .8rem;
        display: flex;
        align-items: center;
        padding: .2rem;
        .ant-tag {
            flex-shrink: 0;
            font-size: .28rem;
            padding: .08rem .10rem;
            height: auto;
            color: #80bd01;
            margin: 0 .2rem;
        }
        .ant-tag-checkable-checked {
            background: #80bd01;
            color: #fff;
        }
        .ant-tag-checkable:active {
            background: transparent;
            color: #80bd01;
        }
        .ant-tag-checkable::selection{
        background: transparent;
        color: #80bd01;
        }
    }
}
@mixin topic-list {
    .topic-list {
        .avatar-img {
            width: .6rem;
            height: .6rem;
        }
        .list-table {
            .ant-table-row {
                font-size: .3rem;
            }
            .list-title {
                max-width: 660px;
            }
        }
    }
}
@mixin pagination {
    .pagination {
        .ant-pagination {
            font-size: .36rem;
            .ant-select {
                font-size: .36rem;
            }
        }
    }
}